import { FinalResultsComponent } from './final-results/final-results.component';
import { Component } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {

  numQes = 0
  results = 0
  myAns= []

  fakeData = [
    {
      id: 1,
      title: 'هل مصر جميلة؟',
      ans: true
    },
    {
      id: 2,
      title: ' هل مصر وحشه؟',
      ans: false
    },
    {
      id: 3,
      title: 'هل مصر جميلة جدا؟',
      ans: true
    },
    {
      id: 4,
    }

  ]

  constructor(public dialogDisplayResults : MatDialog) {
    
  }
  ngOnInit(): void {
  }

  trueAns() {
    if (this.fakeData[this.numQes].ans) {
      this.results = this.results + 10
      this.myAns.push(true)
    } else if ( this.fakeData[this.numQes].ans && this.results != 0){
      this.results = this.results - 10
      this.myAns.push(false)
    }
    this.numQes = this.numQes + 1
    console.log('object :>> ', this.results);
    console.log('object :>> ', this.myAns);

    if (this.numQes === 3){
      this.openDialog(this.results)
    }
  }

  falseAns() {
    if (this.fakeData[this.numQes].ans &&  this.results === 0) {
      this.results = 0
      this.myAns.push(false)
    } else if (this.fakeData[this.numQes].ans && this.results != 0){
      this.results = this.results - 10
      this.myAns.push(false)
    } else if (!this.fakeData[this.numQes].ans){
      this.results = this.results + 10
      this.myAns.push(false)
    }
     else {
      this.results = this.results + 10
      this.myAns.push(true)
    }
    this.numQes = this.numQes + 1
    console.log('object :>> ', this.results);
    console.log('object :>> ', this.myAns);
    if (this.numQes === 3){
      this.openDialog(this.results)
    }

  }

  openDialog(finalResult){
    this.dialogDisplayResults.open(FinalResultsComponent,{
      height: 'auto',
      width: '300px',
      data : {
        dataKey : finalResult
      }
    });

  }


}
